
import LoadMore from 'vux/src/components/load-more/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
data() {
return {
isLoading: true,
hasContent: false,
state: ['0', '1', '2'], //
goods: [],
stateActive: '',
page: 0,
fetching: true,
noMore: false,
}
},
created() {
this.createdDate(0)
},
mounted() {
window.addEventListener("scroll", this.handleScroll);
},
methods: {
createdDate(index) {
this.page = 0;
this.fetching = true;
this.noMore = false;
document.documentElement.scrollTop = 0;
document.body.scrollTop = 0;
this.isLoading = true
this.stateActive = this.state[index]
this.$http.get('/api/getGoodList?status=' + this.stateActive + '&page=' + this.page)
.then((res) => {
this.goods = res.data.data
this.isLoading = false
if (this.goods.length) {
this.hasContent = true
}
})
.catch((err) => {
console.log(err)
})
},
handleScroll() {
let that = this
let heightTop = document.documentElement.scrollTop || document.body.scrollTop;

if (heightTop + window.innerHeight >= document.body.offsetHeight) {

if (this.fetching == true) {
console.log('2222')

this.fetching = false;
this.page++
console.log(this.goods)
this.$http.get('/api/getGoodList?status=' + this.stateActive + '&page=' + this.page)
.then(function(res) {
if (res.data.data.length > 0) {
that.goods = that.goods.concat(res.data.data)

that.fetching = true;
} else {
that.noMore = true
}
})
.catch(function(error) {
console.log(error);
});
}
}
}
},
computed: {

},
components: {
Tab,
TabItem,
LoadMore
},
beforeDestroy() {
window.removeEventListener("scroll", this.handleScroll);
}
}




